
import Logo from './Logo'
import {mapState} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'


export default {
components: {
Logo,
Group,
Cell,
XInput,
XButton,
FormPreview
},
props: ['type', 'param'],
data: function () {
return {
validAmt: function (amt) {
const isValidAmt = /^\d{0,4}(\.\d{0,2})?$/.test(amt);
if (amt && isValidAmt)
return {valid: true}
else
return {valid: false, msg: '金额错误'}
},
tranAmt: ''
}
},
computed: {
...mapState({
url: state => state.url,
cardNo: state => state.cardNo
}),
list: function () {
const param = this.$props.param
param.type = this.$props.type
return Object.keys(param)
.map((key) => ({label: key, value: param[key]}))
}
},
methods: {
click: function () {
const url = `${this.url}/api/sgqr`
const param = this.$props.param
param.type = this.$props.type
param.tranAmt = this.$data.tranAmt
param.cardNo = this.cardNo

const validAmt = this.$data.validAmt(param.tranAmt)
if (validAmt.valid) {
this.$store.commit('UPDATE_LOADING', true)
this.$http.post(url, param)
.then((resp) => {
if (resp.status === 200) {
this.$router.replace({
name: 'result',
params: resp.data
})
} else {
this.$vux.toast.show({
type: 'warn',
position: 'default',
text: resp.status
})
}
})
.catch(() => {
this.$store.commit('UPDATE_LOADING', false)
this.$vux.toast.show({
type: 'warn',
position: 'default',
text: '发送失败'
})
})
} else {
this.$vux.toast.show({
type: 'warn',
position: 'default',
text: validAmt.msg
})
}
}
}
}



